// default export component 
class App extends Component {
  constructor(){
    super();

    this.state = {
      searchValue:"Search by Date"
    }
  }
  componentDidMount(){
    var elems = document.querySelectorAll('.datepicker');
     var instances = M.Datepicker.init(elems);
  }
  render(){
    return (
      <div className="App">
        <div className="chip close">
          <a href="https://www.google.com" style={{color:"blue",fontSize: `16px`}}>close window</a>&nbsp;
          <i className="small material-icons">cancel</i>
      </div>
      <div className="container">
         {/* title-row */}
          <div className="row title-row">
              <div className="col s12 l6 m12">
                  <p className="title">Tech News</p>
              </div>
              <div className="col s12 l6 m12" style={{display: "flex",alignItems: "center"}}>
                  <div className="search-btn">
                      <i className="material-icons">search</i>&nbsp;&nbsp;
                      <input type="text" className="datepicker" value={this.state.searchValue} onChange={e => this.setState({searchValue:e.target.value})}/>
                 </div>
                  <a href="https://www.google.com"><i className="medium material-icons"
                          style={{marginLeft: `50px`,color:"#9519b481"}}>account_circle</i></a>
                   </div>
               </div>
          {/* TechNews-row */}
          <div className="news-row z-depth-5">
              <p className="label">Login to post News</p>
              <div className="container">
              <Feed />
              <Feed />
              <Feed />
              <Feed />
              </div>
           </div>
          {/* close-container */}
          </div>
        </div>
    );
    
  }
}

export default App;
